import { h, Component } from "preact";
import { database } from "../../firebase";
import LayoutGrid from "preact-material-components/LayoutGrid";
import Icon from 'preact-material-components/Icon';
import List from "preact-material-components/List";
import Loader from "../loader";
import Progress from "preact-progress";
import "preact-material-components/List/style.css";
import "preact-material-components/LayoutGrid/style.css";
import style from "./style.css";

export default class Experience extends Component {
  constructor() {
    super();
    this.state = {
      data: null
    };
  }
  componentDidMount() {
    database.ref("experience").on("value", snapshot => {
      this.setState({
        data: snapshot
          .val()
          .filter(item => item !== null)
          .sort((a, b) => {
            return a.order - b.order;
          })
      });
    });
  }

  getAllMonths(start, end){
    let s =  +new Date(start)
    let e = end === 'present time' ?  +new Date(): +new Date(end);
    let result = (e - s) / 2592000000;

    return result > 12 ? `${result.toFixed()/12 >> 0} year(s) ${result.toFixed() - ((result.toFixed()/12 >> 0)*12)} month(s)`
      : `${result.toFixed()} month(s)`;
  };

  render() {
    let { data } = this.state;

    return (
      <section>
        {data === null ? (
          <Loader />
        ) : (
          <LayoutGrid>
            <h1 class="text-center mt-5">Expirience</h1>
            <LayoutGrid.Inner>
              {data.map((item, key) => (
                <LayoutGrid.Cell
                  cols="12"
                  phoneCols={12}
                  tabletCols={12}
                  key={key}
                >
                  <div class={style.item}>
                    <h2>{item.position}</h2>
                    <p className={`date`}>
                      <span><Icon>date_range</Icon>  <span>{item.from} - {item.to}.  {this.getAllMonths(item.from, item.to)}</span></span>
                    </p>
                    <p>Company: {item.company}</p>
                    <p>{item.responsibility}</p>
                  </div>
                </LayoutGrid.Cell>
              ))}
            </LayoutGrid.Inner>
          </LayoutGrid>
        )}
      </section>
    );
  }
}



